@forward 'globals';
html {
	font-family: var(font);
	color: var(--text-color);
	background-color: var(--background-color);
}
.cont1 {
	max-width: 1440px;
	height: 426px;
	padding-top: 96px;
	margin: 0 auto;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-pack: center;
	-ms-flex-pack: center;
	justify-content: center;
	-webkit-box-align: start;
	-ms-flex-align: start;
	align-items: flex-start;
	gap: 95px;
}
.lorem {
	font-size: 42px;
	margin-bottom: 24px;
	font-weight: 700;
}
.summary {
	font-size: 16px;
	line-height: 1.6;
	font-weight: 300px;
}
.art1 {
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	justify-content: flex-start;
	max-height: 250px;
	max-width: 380px;
	margin-top: 30px;
	line-height: 1.6;
	-webkit-box-ordinal-group: 2;
	-ms-flex-order: 2;
	order: 2;
}
.img1 {
	-webkit-box-ordinal-group: 1;
	-ms-flex-order: 1;
	order: 1;
}
.beach {
	width: 505px;
	height: 330px;
}
.buttonbox {
	display: flex;
	order: 3;
	width: auto;
	height: auto;
	align-content: center;
	justify-content: center;
	margin-top: 32px;
}
button.openAlertButton {
	background-color: #E4C1AE;
	height: 48px;
	width: 128px;
	border-radius: 32px;
	border-width: 0px;
	color: #FFFFFF;
	position: relative;
	cursor: pointer;
	transition: 0.3s;
	font-weight: 700;
	font-size: 16px;
	&:hover {
		background-color: #705F55;
		height: 52px;
		width: 132px;
	}
}
.overlay {
	position: fixed;
	display: flex;
	justify-content: center;
	top: 0%;
	left: 0%;
	width: 100%;
	height: 100%;
	background: rgba(0, 0, 0, .7);
	z-index: 10;
	display: none;
	opacity: 0;
	transition: opacity 0.5s;
}
.alert {
	position: relative;
	display: flex;
	flex-direction: row;
	align-items: flex-start;
	justify-content: center;
	max-width: 920px;
	min-width: 190px;
	height: 210px;
	background-color: #FFFFFF;
	z-index: 11;
	top: 40vh;
	opacity: 0;
	transition: opacity 0.5s;
	gap: 317px;
}
.alertcontent {
	margin-top: 56px;
	margin-left: 95px;
	display: flex;
	flex-direction: column;
	gap: 24px;
	height: 98px;
	width: 380px;
}
.counter {
	font-weight: bold;
}
.close {
	color: #7a7a7a;
	font-size: 30px;
	z-index: 12;
	cursor: pointer;
	transition: 0.3s;
	order: 1;
	align-self: flex-end;
	&:hover {
		color: #000000;
	}
}
.headalert {
	font-size: 42px;
	font-weight: bold;
	color: #262524;
}
.resetCounterButton {
	background-color: #E4C1AE;
	height: 48px;
	width: 128px;
	border-radius: 20px;
	border-width: 0px;
	color: #FFFFFF;
	font-weight: 700;
	cursor: pointer;
	transition: 0.3s;
	display: none;
	&:hover {
		background-color: #705F55;
		height: 52px;
		width: 132px;
	}
}
.buttonbox2 {
	display: flex;
	flex-direction: column;
	width: 128px;
	height: 48px;
	order: 2;
}
.buttonscontainer {
	display: flex;
	flex-direction: column;
	justify-content: center;
	gap: 72px;
	margin-top: 30px;
	margin-right: 42.5px;
	width: 128px;
	height: 150px;
}
@media all and (max-width: 1000px) {
	.cont1 {
		gap: 35px;
	}
	.lorem {
		font-size: 40px;
		margin-bottom: 14px;
	}
	.summary {
		font-size: 14px;
	}
	.art1 {
		max-height: 350px;
		max-width: 280px;
		margin-top: 0px;
	}
	.beach {
		width: 455px;
		height: 280px;
	}
	.buttonbox {
		margin-top: 12px;
	}
	button.openAlertButton {
		height: 40px;
		width: 118px;
		font-size: 14px;
		&:hover {
			background-color: #705F55;
			height: 42px;
			width: 122px;
		}
	}
	.alert {
		max-width: 750px;
		min-width: 190px;
		gap: 170px;
	}
	.alertcontent {
		height: 98px;
		width: 450px;
	}
}
